.popup {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background: rgb(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
    z-index: 1;
}